.container {
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 32px;
    max-width: 768px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    padding: 0 72px;
    max-width: 1440px;
  }
}

.title {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 700;
  font-size: 50px;
  line-height: 1.22;

  text-align: center;

  color: #d9d9d9;

  padding-top: 177px;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 80px;
    line-height: 1.21;

    padding-top: 224px;
  }
}

@media screen and (min-width: 1440px) {
  .title {
    font-size: 120px;
    line-height: 1.21;

    padding-top: 188px;
  }
}

.text {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.62;

  text-align: center;

  color: #4a4c4c;

  max-width: 80%;
}

@media screen and (min-width: 768px) {
  .text {
    font-size: 20px;
    line-height: 1.3;

    min-width: 440px;
    max-width: 441px;
  }
}
@media screen and (min-width: 1440px) {
  .text {
    font-size: 20px;
    line-height: 1.5;

    min-width: 878px;
    max-width: 879px;
  }
}

.number {
  width: 112px;
  height: 45px;

  margin-top: 16px;
  margin-bottom: 177px;
}

@media screen and (min-width: 768px) {
  .number {
    width: 158px;
    height: 56px;

    margin-top: 32px;
    margin-bottom: 216px;
  }
}

@media screen and (min-width: 1440px) {
  .number {
    width: 222px;
    height: 74px;

    margin-top: 40px;
    margin-bottom: 189px;
  }
}
